.Layout {
    width:100%;
    height: 1080px;
    display: grid;
    grid-template-columns: 200px auto;
    grid-template-areas:
      "aside mainbg"
  }
  
  .mainbg {
    grid-area: mainbg;
    width: auto;
    height: 1080px;
    border:12px;
  }
  
  .Sidebar {
    grid-area: aside;
    width: 200px;
    height: 1080px;
    background: linear-gradient(
      180deg,
      #e26a6a 0%,
      #e26a6a 0%,
      rgba(222.76, 105.23, 108.68, 0.99) 2%,
      rgba(200.56, 102.86, 127.86, 0.92) 15%,
      rgba(55.84, 87.37, 252.88, 0.46) 100%
    );
    position: fixed;
    flex-direction: column;
    border-right: 2px solid #000;
    display: flex;
  }

  /*Main Page 시작*/

  .MainWallpaper {
    width: 100%;
    height: 100%;
    background-color: orange;
    justify-content: center;
    align-items: center;
  }

  /*Search bar*/
  .header {
    margin: 0;
    padding: 30px;
    background-color: #b2e0df;
    display: flex;
    justify-content: center;
    align-items: center;
    position: sticky;
  }

  .Dalle_createbar{
    width: 50vw;
    z-index:1;
     display: inline-block;
     font-size: 26px;
     font-weight: bold;
     line-height: 1;
     padding: 15px 15px;
     border: 1px solid #1e1e29;
     color: #1e1e29;
     text-align: center;
     text-decoration: none;
     transition: all .5s;
     position: relative;
     display: inline-block;
     overflow: hidden;
     margin: 0 10px;
     text-transform: uppercase;
     box-sizing: border-box;
     background-color: transparent;
  }
  /*Create your Image!*/
.Createbtn{
    z-index:1;
     display: inline-block;
     font-size: 26px;
     font-weight: bold;
     line-height: 1;
     padding: 15px 15px;
     border: 1px solid #1e1e29;
     color: #1e1e29;
     text-align: center;
     text-decoration: none;
     transition: all .5s;
     position: relative;
     display: inline-block;
     overflow: hidden;
     margin: 0 10px;
     text-transform: uppercase;
     box-sizing: border-box;
     background-color: transparent;
 }
 .Createbtn:before,.Createbtn:after{
   content: '';
     position: absolute;
     width: 0;
     height: 0;
     border: 0 solid;
     -webkit-transform: rotate(360deg);
     transform: rotate(360deg);
    transition: all 0.5s ease-in-out;
 }
 .Createbtn:before{
    bottom: 0;
     left: 0;
     border-color: transparent transparent transparent #1e1e29;
 }
 .Createbtn:after{
     border-color: transparent #1e1e29 transparent transparent;
     position: absolute;
     top: 0;
     right: 0;
 }
 .Createbtn:hover{
   color: #fff !important;
   opacity: 1 !important;
   cursor: pointer;
 }
 .Createbtn:hover:before,.Createbtn:hover:after{
   border-width: 70px 220px !important;
   -webkit-transition: 0.5s ease-in-out;
   transition: 0.5s ease-in-out;
   z-index: -1;
 }
