/* RESULT Page */
/* 레이아웃 깨짐; 시간 없으니 이대로 일단 ㄱㄱ 건드리지마셈 */
.Result {
    width: auto;
    height:100%;
    background: lightcoral;
    display: grid;

    grid-template-rows: repeat(2, 100px);
    grid-template-columns: repeat(2, 700px);
    grid-gap: 15px;
  }

  .Result-img {
    background-color: #333232;
    width: 512px;
    height: 512px;
    object-fit: fill;
    margin-top: 100px;
    margin-left: 100px;
  }
  
  
  .Result-prompt {
    background: linear-gradient(to right, #8e9297, #bdc3c7);
    width: 500px;
    height: 50px;
    margin-top: 100px;
    margin-bottom: 15px;
  }
  
  .Result-title {
    background: linear-gradient(to right, #8e9297, #bdc3c7);
    width: 500px;
    height: 50px;
    margin-bottom: 15px;
  }
  
  .Result-content {
    background: linear-gradient(to right, #8e9297, #bdc3c7);
    width: 500px;
    height: 150px;
    margin-bottom: 15px;
  }
  
  .Result-comment {
    background: linear-gradient(to right, #2c3e50, #bdc3c7);
    width: 500px;
    height: 300px;
    margin-bottom: 10px;
  }
  .Result-button {
    width: 500px;
    height: 300px;
  }
  
  .Result-btn-group > button {
    float: right;
    color: #fff;
    background-color: #000;
    width: 9rem;
    height: 2.5rem;
    box-shadow: 1px 1px 1px 1px lightgray;
    border-radius: 4px;
    margin-right: 5px, 5px, 5px;
  }
  
  /* My Page */
  .MyPage-container {
    background-color: green;
    width:100%;
    height:100%;
    display: grid;
    grid-template-columns: 30vw auto;
    grid-template-areas: "profilebio profilegrid";
  }

  .profile-container {
